import React, { useState, useEffect } from "react";

const GeoLocation = () => {
  const [latlong, setLatLong] = useState([]);

  useEffect(() => {
    // if (latlong.length === 0) {
    //   localStorage.getItem("Values");
    //   if (
    //     localStorage.getItem("Values") !== undefined &&
    //     localStorage.getItem("Values") !== null
    //   ) {
    //     setLatLong(localStorage.getItem("Values").split(","));
    //   }
    // } else {
    //   localStorage.setItem("Values", latlong);
    // }
    // if (latlong.length > 25) {
    //   setLatLong([latlong[latlong.length - 1]]);
    //   localStorage.setItem("Values","");
    // }
  }, [latlong]);
  //   //function that gets the location and returns it
  function getLocation() {
    setInterval(() => {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(showPosition);
      } else {
        console.log("Geo Location not supported by browser");
      }
    }, 1000 * 5);
  }

  //   //function that retrieves the position
  function showPosition(position) {
    var location = {
      longitude: position.coords.longitude,
      latitude: position.coords.latitude,
    };
    setLatLong([
      ...latlong,
      "Lat:" + location.latitude + " Long" + location.longitude,
    ]);
    //Make API call to save location
  }
  //   //request for location

  const searchBluetooth = () => {
    if (!("bluetooth" in navigator)) {
      setLatLong([...latlong, "Bluetooth API not supported."]);
      return;
    }

    navigator.bluetooth
      .requestDevice({
        acceptAllDevices: true
      })
      .then(function (device) {
          alert("Device" +  device.name)
        return device.gatt.connect();
      })
      .catch(function (error) {
        setLatLong([...latlong, error.message]);
      });
  };

  //   const onClickLocation = () => {
  //     setInterval(() => {
  //       if ("geolocation" in navigator) {
  //         navigator.geolocation.getCurrentPosition(function (location) {
  //           setLatLong([
  //             ...latlong,
  //             "Lat:" +
  //               location.coords.latitude +
  //               " Long" +
  //               location.coords.longitude,
  //           ]);
  //         });
  //       } else {
  //         setLatLong([...latlong, "Geolocation API not supported."]);
  //       }
  //     }, 1000 * 5);
  //   };

  return (
    <div>
      <div>You are safe</div>
      {getLocation()}
      {/* {latlong} */}
      {/* <button id="askButton" onClick={() => onClickLocation()}>
        Ask for location
      </button> */}
      <button id="askButton" onClick={() => searchBluetooth()}>
        Ask for Bluetooth
      </button>
      <br></br>
      {latlong.map((item) => {
        return (
          <div>
            {item}
            <br></br>
          </div>
        );
      })}
    </div>
  );
};

export default GeoLocation;
